import axios from 'axios';
import toast from './toast'

export default function setup() {
    axios.interceptors.request.use(function(config) {
        document.getElementsByTagName("body")[0].style.cursor="wait";
        config.headers['Content-Type'] = 'application/json';return config;}, function(err) {
        return err;
    });
    axios.interceptors.response.use(function(response){
        document.getElementsByTagName("body")[0].style.cursor="default";
        return response},function(error){
        var objErrors=JSON.stringify(error.response.data.errors);
        switch(error.response.status){
            case 400:{ //Bad request
                toast.error(objErrors);
                break;
            }
            case 401:{ //Unauthorized
                toast.error(objErrors);
                break;
            }
            case 404:{ //Not Found
                toast.error(objErrors);
                break;
            }
            case 422:{ //UnprocessableEntity
                var errMsgs="";
                error.response.data.errors.forEach(msg => {
                    var res=JSON.stringify(msg.message);
                    errMsgs+=res+"\n";
                });
                toast.error(errMsgs);
                break;
            }
            case 500:{//Internal Server Error
                toast.error(objErrors);
                break;
            }
        }
    })
}