import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Register.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Register',
        component: Home
    },
    {
        path: '/manufacturer',
        name: 'ManufacturerPreview',
        component: () =>
            import ( /* webpackChunkName: "manufacturer" */ '../views/Manufacturer.vue')
    },
    {
        path: '/successfullyregistered',
        name: 'successfullyregistered',
        component: () =>
            import ( /* webpackChunkName: "successfullyregistered" */ '../views/SuccessfullyRegistered.vue')
    },
    {
        path: '/Register/:Guid',
        name: 'RegisterWithSavedData',
        props: true,
        component: Home
    },
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router