                                                                        <label for="username" class="col-form-label">Region : </label><span class="col-md-8"><b>{{location.warehouse.address.region.name}}</b></span><br>
                                                                        <label for="username" class="col-form-label col-md-4">Postal Address : </label><span class="col-md-8"><b> {{location.warehouse.address.postalAddress}}</b> </span><br>
                                                                    </div>
