                                                                    <div class="col-md-5">
                                                                        <label for="username" class="col-form-label col-md-4">Name : </label><span class="col-md-8"><b>{{location.warehouse.contact.name}}</b></span><br>
                                                                        <label for="username" class="col-form-label col-md-4">Phone Number : </label><span class="col-md-8"><b> {{location.warehouse.contact.phone}} </b></span><br>
                                                                        <label for="username" class="col-form-label">Email ID : </label><span class="col-md-8"><b>{{location.warehouse.contact.email}}</b></span><br>

                                                                    </div>
