                                                                    <a type="button" v-on:click="editWarehouseDto(location.warehouse.name)"><img src="@/assets/images/icons/edit-gray.png" class="mr-1"><span class="text-primary"><b>Edit  </b></span></a>
                                                                    <a type="button" v-on:click="removeWarehouseDto(location.warehouse.name)"><img src="@/assets/images/icons/cross-red.png" class="mr-1"><span class="text-primary"><b>Remove</b></span></a>
                                                                </p>
                                                            </div>
                                                        </div>
                                                        <div v-if="warehouseListErros">
                                                            <span class="text-danger" v-if="!$v.warehouses.checkNullOrEmpty">Add atleast one Warehouse</span>

                                                        </div>
                                                    </div>
                                                </div>

                                                <button class="btn btn-primary float-right mb-4 pl-3 pr-3 p-2 mr-5 mt-4" name="save" value="SAVE" v-on:click="saveAccordion5()" type="button"><img src="@/assets/images/icons/save-new.png" class="mr-2 ic-wd" />SAVE</button>
                                            </form>
                                        </div>
                                    </div>
                                </b-card-body>
                            </b-collapse>
                        </b-card>

                        <b-card no-body class="mb-1">
                            <b-card-header header-tag="header" id="header-6" type="button" class="p-1" v-on:click="toggleAccordion(6)">
                                <div class="card-header" id="headingOne">
                                    <table class="table">
                                        <tbody>
                                            <tr>
                                                <td class="td-mt wid-20">06</td>
                                                <td class="td-mt wid-30">Upload Compliance Documents</td>
                                                <td class="td-mt ">
                                                    <img v-if="accordion6" src="@/assets/images/icons/done.png" class="ic-wd" />
                                                    <img v-else src="@/assets/images/icons/exclamation.png" class="ic-wd exclamation" />
                                                </td>
                                                <td class="float-right td-mt">
                                                    <button class="btn btn-link"><img src="@/assets/images/icons/down-arrow.png" /></button>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </b-card-header>
                            <b-collapse id="accordion-6" accordion="my-accordion" role="tabpanel">
                                <b-card-body>
                                    <div id="collapseOne" aria-labelledby="headingOne" data-parent="#accordion">
                                        <div class="p-3 ml-5 mr-5 p-20">
                                            <form>
                                                <center>
                                                    <div class="fallback mt-5 mb-5">
                                                        <div class="image-upload">
                                                            <label for="file-input">
                                                                <img src="@/assets/images/icons/upload.png" />
                                                            </label>

                                                            <input id="file-input" ref="file" type="file" v-on:change="selectFilesToUpload()" multiple accept="application/pdf,.docx" name="file" />
                                                        </div>
                                                        <label for="file-input" class="btn btn-outline-primary mt-4 pl-5 pr-5 p-2"><b> Select File</b></label>
                                                        <p class="text-muted mt-2">Maximum Upload File Size: 2 MB.</p><br>
                                                        <hr class="dot1 col-md-12">
                                                        <div class="form-group" :class="{ 'form-group--error': $v.uploadedFiles.$error }">
                                                            <div class="col-md-12 form-row text-left" v-for="(uploadFile, index) in uploadedFiles" :key="index">

                                                                <div class="col-md-4 pl-0">
                                                                    <p class="mt-1 text-muted"> <img src="@/assets/images/icons/pdf.png" class="mr-2" />{{uploadFile.fileName}}</p>
                                                                </div>
                                                                <div class="col-md-3">
                                                                    <p class="mt-3 text-muted" v-if="uploadFile.fileSize < 1024 * 1024">{{(((uploadFile.fileSize)/1024)).toFixed(2)}} KB</p>
                                                                    <p class="mt-3 text-muted" v-else>{{(((uploadFile.fileSize)/1024)/1024).toFixed(2)}} MB</p>
                                                                </div>
                                                                <div class="col-md-3">
                                                                    <p class="mt-3 text-muted">{{uploadFile.createdDateTime | FormatingDate}}</p>
                                                                </div>
                                                                <div class="col-md-1 text-right">
                                                                    <p class="mt-1 text-muted"><img src="@/assets/images/icons/cross-red.png" class="mt-2" @click="removeSelectedFiles(index)" /></p>
                                                                </div>
                                                            </div>
                                                            <hr class="dot col-md-12">
                                                            <div class="">
                                                                <!--custom-control custom-checkbox-->
                                                                <input type="checkbox" class="custom-control-input" id="checkbox-signin" v-model="fileUploadAcknowledge">
                                                                <label class="custom-control-label" for="checkbox-signin">I knowledge that I have Read, and do hereby accept the <a href="#" class="text-primary"><b>Term & Conditions</b></a>  </label>
                                                            </div>
                                                            <div v-if="uploadFilesErrors">
                                                                <span class="text-danger" v-if="!$v.uploadedFiles.checkNullOrEmpty">Select aleast 1 file</span>
                                                                <span class="text-danger" v-else-if="!$v.fileUploadAcknowledge.IsChecked">Please read and accept Terms Conditions..</span>
                                                            </div>
                                                        </div>
                                                        <button class="btn btn-primary mb-4 pl-3 pr-3 p-2 mt-5" name="save" value="SAVE" v-on:click="saveAccordion6()" type="button"><img src="@/assets/images/icons/save-new.png" class="mr-2 ic-wd" />Upload</button>
                                                    </div>
                                                </center>
                                            </form>
                                        </div>
                                    </div>
                                </b-card-body>
                            </b-collapse>
                        </b-card>

                        <b-card no-body class="mb-1">
                            <b-card-header header-tag="header" id="header-7" type="button" class="p-1" v-on:click="toggleAccordion(7)">
                                <div class="card-header" id="headingOne">
                                    <table class="table">
                                        <tbody>
                                            <tr>
                                                <td class="td-mt wid-20">07</td>
                                                <td class="td-mt wid-30">Comments</td>
                                                <td class="td-mt ">
                                                    <img v-if="accordion7" src="@/assets/images/icons/done.png" class="ic-wd" />
                                                    <img v-else src="@/assets/images/icons/exclamation.png" class="ic-wd exclamation" />
                                                </td>
                                                <td class="float-right td-mt">
                                                    <button class="btn btn-link"><img src="@/assets/images/icons/down-arrow.png" /></button>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </b-card-header>
                            <b-collapse id="accordion-7" accordion="my-accordion" role="tabpanel">
                                <b-card-body>
                                    <div id="collapseOne" aria-labelledby="headingOne" data-parent="#accordion">
                                        <div class="p-3 ml-5 mr-5 p-20">
                                            <form method="post" action="#">
                                                <div class="col-12 col-sm-12 p-0 mt-3" v-if="approvals.length>0">
                                                    <p class="text-muted mb-2">History</p>
                                                    <div class="table-responsive">
                                                        <table class="table layout-secondary bordered">
                                                            <thead>
                                                                <tr>
                                                                    <th>Approver</th>
                                                                    <th>Action</th>
                                                                    <th>ActionBy</th>
                                                                    <th>Comment</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                <template v-for="(approval) in approvals">
                                                                    <tr v-for="(History,index) in approval.approvalHistories" :key="index">
                                                                        <td v-if="approval.approver===1">GRA</td>
                                                                        <td v-else>TSSP</td>
                                                                        <td>{{History.action}}</td>
                                                                        <td v-if="History.actionedBy==='Manufacturer'">{{manufacturer.name}}</td>
                                                                        <td v-else>{{History.actionedBy}}</td>
                                                                        <td>{{History.comment}}</td>
                                                                    </tr>
                                                                </template>
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                                <div class="col-12 col-sm-12 mt-4">
                                                    <div class="form-group">
                                                        <p class="text-muted mb-2"> Comments</p>
                                                        <div class="form-group" :class="{ 'form-group--error': $v.newComment.$error }">
                                                            <textarea class="form-control" name="comments" rows="5" v-model="newComment" placeholder="Comments" :disabled="disableCommentSection"></textarea>
                                                            <div v-if="commentErrors">
                                                                <span class="text-danger" v-if="!$v.newComment.required">comment is required</span>
                                                            </div>
                                                        </div>
                                                    </div>
                                                    <button :disabled="disableCommentSection" class="btn btn-primary btn-lg float-right mb-4 pl-3 pr-3 p-2 mt-3 " name="save" v-on:click="saveAccordion7()" type="button"><img src="@/assets/images/icons/save-new.png" class="mr-2 ic-wd" />Save</button>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </b-card-body>
                            </b-collapse>
                        </b-card>
                        <button :disabled="!showPreview()" class="btn btn-primary btn-lg float-right mb-4 pl-5 pr-5 p-3 mt-3 mr-3" name="preview" value="PREVIEW" type="button" v-on:click="previewManufacturerDto();" v-b-tooltip.hover title="Please fill all the Details">Preview</button>
                    </div>
                </div>
            </div>
            <!-- END: Card DATA-->
        </div>

    </div>
</template>

<script>
    import { required, alphaNum, email, sameAs } from 'vuelidate/lib/validators';
    import Loader from "../popup/loader";
    import toast from '@/toast';
    import axios from 'axios';
    import { checkNullOrEmpty, checkEmptyNumber, checkEmailDuplicate,checkMfNameDuplicate,checkMfTinDuplicate,checkMfLicDuplicate } from '@/Validations';
    const { BlobServiceClient } = require("@azure/storage-blob");
    export default {
        data() {
            return {
                //temp properties
                mfGUID: '', searchSupplierName: '', manufacturerConfirmEmailId: '', warehouseConfirmEmailId: '', manufacturerName: '',
                objSupplier: this.initializeSupplierObj(), objWareHouse: this.initializeWareHouseObj(), fileUploadAcknowledge: false,
                objBprod: this.initializeBproductsObj(), isEditing: false, disableCommentSection: false,
                objFileUpload: this.initializeFileUploadObj(), mfSelectedCountry: '', mfRegions: [], mfSelectedRegion: '',
                //action properties
                manufacturer: this.initializeMfObj(),
                manufacturerDto: {}, products: [], brandProducts: [], suppliers: [], warehouses: [],
                fileUploadId: 0, uploadedFiles: [], mfGuid: '', id: '', lastEditedScreen: '', newComment: null, approvals: [],
                //validation flags
                manufacturerNameErrors: false, manufacturerBasicErrors: false, brandProductErrors: false, brandProductListErrors: false,productChoosedErrors:false,
                supplierErrors: false, supplierListErrors: false, warehouseErrors: false, warehouseListErros: false, uploadFilesErrors: false, commentErrors: false,
                //accordion flags
                accordion1: false, accordion2: false, accordion3: false, accordion4: false, accordion5: false, accordion6: false, accordion7: false,
                accordion1error: false,
                //show collpase
                showNewBProducts: false, showNewSupplier: false, showOldSupplier: false, showNewWarehouse: false
            }
        },
        components: {
            Loader
        },
        props: ['Guid'],
        async mounted() {
            await this.$store.dispatch("getProductsAsync");
            await this.$store.dispatch("getSuppliersAsync");
            await this.$store.dispatch("getCountriesAsync");
            await this.getManufacturerDto();
            if (this.Guid !== undefined && this.lastEditedScreen != 'accordion-7')
                this.$root.$emit('bv::toggle::collapse', '' + this.lastEditedScreen + '');
            else if (this.Guid !== undefined && this.lastEditedScreen === 'accordion-7' && !this.disableCommentSection)
                this.$root.$emit('bv::toggle::collapse', '' + this.lastEditedScreen + '');
        },
        computed: {
            sasToken() {
                return this.$store.getters.sasToken;
            }
        },
        filters: {
            FormatingDate: function (value) {
                if (value == null || value == '') return '';
                var date = new Date(value);
               var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
                return ('0' + date.getDate()).slice(-2) + "-" + months[date.getMonth()]  + "-" + date.getFullYear();
            }
        },
        validations: {
            manufacturerName: { required,
            checkMfNameDuplicate(value) {
                            if (this.manufacturer.mfId !== 0) return true;
                            return checkMfNameDuplicate(value);
                        }
             },
            manufacturerConfirmEmailId: { required, email, sameAs: sameAs(function () { return this.manufacturer.contact.email }) },
            warehouseConfirmEmailId: { required, email, sameAs: sameAs(function () { return this.objWareHouse.warehouse.contact.email }) },
            mfSelectedCountry: { required, checkNullOrEmpty },
            mfSelectedRegion: { required, checkNullOrEmpty },
            manufacturer: {
                tinNumber: {
                    required, alphaNum,
                    checkMfTinDuplicate(value) {
                            if (this.manufacturer.mfId !== 0) return true;
                            return checkMfTinDuplicate(value);
                        }
                },
                exciseLicenseNumber: {
                    required, alphaNum,
                    checkMfLicDuplicate(value) {
                            if (this.manufacturer.mfId !== 0) return true;
                            return checkMfLicDuplicate(value);
                        }
                },
                contact: {
                    email: {
                        required, email,
                        checkEmailDuplicate(value) {
                            if (this.manufacturer.mfId !== 0) return true;
                            return checkEmailDuplicate(value);
                        }
                    },
                    phone: { required, },
                    name: { required },
                    countryCode: { required }
                },
                address: {
                    description: { required },
                    postalAddress: { required },
                }
            },
            products:{
                checkProducts(value){
                    if(value.filter(element=>element.isActive===true).length===0)return false; return true}},
            objBprod: {
                name: { required, },
                product: { required, checkNullOrEmpty }
            },
            brandProducts: { checkNullOrEmpty },
            objSupplier: {
                supplier: {
                    name: {
                        required,
                        async checkDuplicate(value) {
                            var res = await this.getSupplierAPI(value)
                            return (res === null);
                        }
                    },
                    identifier1: { required },
                    contact: {
                        name: { required },
                        email: {
                            required, email,
                            checkEmailDuplicate(value) {
                                if (this.manufacturer.mfId !== 0) return true;
                                return checkEmailDuplicate(value);
                            }
                        },
                        phone: { required },
                        countryCode: { required }
                    },
                    address: {
                        description: { required },
                        country: { required, checkNullOrEmpty, },
                        postalAddress: { required }
                    }
                }
            },
            suppliers: { checkNullOrEmpty },
            objWareHouse: {
                warehouse: {
                    name: { required },
                    contact: {
                        name: { required },
                        email: {
                            required, email,
                            checkEmailDuplicate(value) {
                                if (this.manufacturer.mfId !== 0) return true;
                                return checkEmailDuplicate(value);
                            }
                        },
                        phone: { required },
                        countryCode: { required }
                    },
                    address: {
                        description: { required },
                        country: { required, checkNullOrEmpty, },
                        region: { required, checkNullOrEmpty },
                        postalAddress: { required },
                        latitude: { checkEmptyNumber },
                        longitude: { checkEmptyNumber }
                    }
                }
            },
            warehouses: { checkNullOrEmpty },
            newComment: { required },
            uploadedFiles: { checkNullOrEmpty },
            fileUploadAcknowledge: {
                IsChecked(value) {
                    return value;
                }
            }
        },
        watch: {
            manufacturerName(value) {
                this.manufacturer.name = value;
            },
        },
        methods: {
            async getSupplierAPI(supplierName) {
                if(supplierName==="") return null;
                try {
                    const response = await axios.get(process.env.VUE_APP_VmApisBaseUrl + '/Supplier/GetSupplierwithName?name=' + supplierName);
                    if(response.data==="") return null;
                    return response.data;
                } catch (error) {
                    console.log(error);
                }
            },
            changeCountry() {
                var selCountry = this.$store.getters.countries.filter(con => con.name === this.mfSelectedCountry);
                this.manufacturer.address.country = selCountry[0];
                this.mfRegions = selCountry[0].regions;
            },
            changeRegion() {
                var selRegion = this.mfRegions.filter(reg => reg.name === this.mfSelectedRegion);
                this.manufacturer.address.region = selRegion[0];
            },
            //property initialize
            initializeMfObj() {
                var obj = {mfId: 0, name: '', tinNumber: '', exciseLicenseNumber: '', isImporter: false,
                    contact: { id: 0, name: '', email: '', countryCode: '+91', phone: '' },
                    address: {
                        id: 0, description: '', postalAddress: '',
                        country: { id: 0, name: '', regions: [] },
                        region: { id: 0, name: '', countryId: 0 },
                        longitude: 0, latitude: 0
                    },
                }
                return obj;
            },
            initializeBproductsObj() {
                var obj = { id: 0, name: '', product: { id: 0, name: '', origin: 1 }, isActive: true };
                return obj;
            },
            initializeApprovalObj() {
                var obj = { id: 0, mfId: 0, approver: 2, action: 0, actionedBy: 0, approvalHistories: [] };
                return obj;
            },
            initializeApprovalHistoryObj() {
                var obj = { id: 0, approvalId: 0, action: '', actionedBy: '', comment: '' };
                return obj;
            },
            initializeFileUploadObj() {
                var obj = { id: 0, file: null, fileSize: 0, fileURL: '', fileName: '', contentType: '', sourceId: 0, attachmentSourceType: 1, uploadedBy: 0, createdDateTime: new Date().toLocaleDateString(), isActive: true };
                return obj;
            },
            initializeWareHouseObj() {
                var obj = {
                    id: 0,
                    warehouse: {
                        id: 0,
                        name: '',
                        contact: { id: 0, name: '', email: '', countryCode: '+233', phone: '' },
                        address: {
                            id: 0, description: '', postalAddress: '',
                            country: { id: 0, name: '', regions: [] },
                            region: { id: 0, name: '', countryId: 0 },
                            longitude: NaN, latitude: NaN
                        }
                    },
                };
                return obj;
            },
            initializeSupplierObj() {
                var obj = {
                    id: 0, supplier:
                    {
                        id: 0, name: '', identifier1: '', identifier2: '',
                        contact: { id: 0, name: '', email: '', countryCode: '+233', phone: '' },
                        address: {
                            id: 0, description: '', postalAddress: '',
                            country: { id: 0, name: '', regions: [] },
                            region: { id: 0, name: '', countryId: 0 },
                            longitude: 0, latitude: 0
                        }
                    }, isActive: true
                };
                return obj;
            },
            async getManufacturerDto() {
                var guid= this.Guid===undefined? this.$store.getters.manufacturerGuid: this.Guid;
                if(guid.length>0){
                    await this.$store.dispatch("getManufacturerDtoSavedDataByGuidAsync", guid);
                    this.manufacturerDto = this.$store.getters.manufacturerDto;
                    this.products = this.selectedProducts(this.manufacturerDto.products);
                    this.brandProducts = this.manufacturerDto.brandProducts;
                    this.assignSupplierfields(this.manufacturerDto.suppliers);
                    this.assignWareHousefields(this.manufacturerDto.warehouses);
                    this.fileUploadId = this.manufacturerDto.fileUploadId;
                    this.uploadedFiles = this.manufacturerDto.uploadedFiles;
                    this.assignApprovalsfields(this.manufacturerDto.approvals);
                    this.mfGuid = this.manufacturerDto.mfGuid;
                    this.id = this.manufacturerDto.id;
                    this.newComment = this.manufacturerDto.comments !== null ? this.manufacturerDto.comments : '';
                    this.lastEditedScreen = this.manufacturerDto.lastEditedScreen;
                    this.assignManfucturerfields(this.manufacturerDto);
                    this.manufacturerName = this.manufacturerDto.name;
                    this.manufacturerConfirmEmailId = this.manufacturerDto.contact.email;
                    this.accordionStatus();
                }
            },
            assignSupplierfields(arrSuppliers) {
                this.suppliers = [];
                arrSuppliers.forEach(element => {
                    var obj = { id: element.id, supplier: element.supplier, manufacturer: {}, isActive: element.isActive };
                    this.suppliers.push(obj);
                });
            },
            assignWareHousefields(arrWarehouses) {
                this.warehouses = [];
                arrWarehouses.forEach(element => {
                    var obj = { id: element.id, manufacturer: {}, warehouse: element.warehouse };
                    this.warehouses.push(obj);
                });
            },
            assignApprovalsfields(arrApproval) {
                this.approvals = [];
                if (arrApproval !== null) {
                    arrApproval.forEach(obApproval => {
                        var approval = { id: obApproval.id, mfId: obApproval.mfId, approver: obApproval.approver, action: obApproval.action, actionedBy: obApproval.actionedBy, approvalHistories: [] };
                        obApproval.approvalHistories.forEach(element => {
                            approval.approvalHistories.push({ id: element.id, approvalId: element.approvalId, action: element.action, actionedBy: element.actionedBy, comment: element.comment });
                        });
                        this.approvals.push(obApproval);
                        if (this.approvals[this.approvals.length - 1].action === 0 || this.approvals[this.approvals.length - 1].action === 3)
                            this.disableCommentSection = false;
                        else
                            this.disableCommentSection = true;
                    });
                }
                else
                    this.disableCommentSection = this.newComment === "" ? false : true;
            },
            assignManfucturerfields(manufacturerDto) {
                this.manufacturer = {
                    mfId: manufacturerDto.mfId, name: manufacturerDto.name, tinNumber: manufacturerDto.tinNumber, exciseLicenseNumber: manufacturerDto.exciseLicenseNumber, isImporter: manufacturerDto.isImporter,
                    contact: { id: manufacturerDto.contact.id, name: manufacturerDto.contact.name, email: manufacturerDto.contact.email, countryCode: manufacturerDto.contact.countryCode, phone: manufacturerDto.contact.phone },
                    address: {
                        id: manufacturerDto.address.id, description: manufacturerDto.address.description, postalAddress: manufacturerDto.address.postalAddress,
                        country: { id: manufacturerDto.address.country.id, name: manufacturerDto.address.country.name, regions: manufacturerDto.address.country.regions },
                        region: { id: manufacturerDto.address.region.id, name: manufacturerDto.address.region.name, countryId: manufacturerDto.address.region.countryId },
                        longitude: manufacturerDto.address.longitude, latitude: manufacturerDto.address.latitude
                    },
                }
                this.mfSelectedCountry = this.manufacturer.address.country.name;
                this.mfSelectedRegion = this.manufacturer.address.region.name;
            },
            accordionStatus() {
                this.$v.$touch();
                this.manufacturerBasicErrors = this.$v.manufacturer.$anyError || this.$v.manufacturerConfirmEmailId.$anyError;
                if (!this.manufacturerBasicErrors) this.accordion1 = true;
                if ((this.products.filter(element => element.isActive === true)).length > 0) this.accordion2 = true;
                if (this.brandProducts.length > 0) this.accordion3 = true;
                if (this.suppliers.length > 0) this.accordion4 = true;
                if (this.warehouses.length > 0) this.accordion5 = true;
                if (this.uploadedFiles.length > 0) this.accordion6 = true;
                if (this.approvals.length > 0 || this.newComment != "") this.accordion7 = true;
            },
            loadProducts() {
                this.products = [];
                this.$store.getters.products.forEach(prod => {
                    var objProd = { id: 0, product: { id: prod.id, name: prod.name, origin: prod.origin }, isActive: false };
                    this.products.push(objProd);
                });
            },
            selectedProducts(arrproducts) {
                this.loadProducts();
                this.products.forEach(prod1 => {
                    arrproducts.forEach(prod2 => {
                        if (prod1.product.id === prod2.product.id)
                            prod1.isActive = prod2.isActive; prod1.id = prod2.id;
                    });
                });
                this.manufacturer.isImporter = this.products.filter(element => element.product.origin === 2).length > 0 ? true : false;
                return this.products;
            },
            //toggle accordions
            async toggleAccordion(accordionId) {
                var activeHeader = document.getElementById('accordion-' + accordionId);
                var classHeader = document.getElementById('header-' + accordionId);
                var allHeaders = document.getElementsByClassName('card-header');
                if (activeHeader.classList == "collapse") {
                    await this.getManufacturerDto();
                    if (this.validateAccordion(accordionId)) {
                        this.$root.$emit('bv::toggle::collapse', 'accordion-' + accordionId);
                        for (var i = 0; i < allHeaders.length; i++) {
                            allHeaders[i].classList.remove('not-collapsed');
                        }
                        classHeader.classList.add('not-collapsed');
                    }
                }
                else {
                    this.$root.$emit('bv::toggle::collapse', 'accordion-' + accordionId);
                    classHeader.classList.remove('not-collapsed');
                }
            },
            async toggleAccordionAuto(accordionId) {
                var allHeaders = document.getElementsByClassName('card-header');
                for (var i = 0; i < allHeaders.length; i++) {
                    allHeaders[i].classList.remove('not-collapsed');
                }
                if (this.validateAccordion(accordionId)) {
                    accordionId = accordionId + 1;
                    this.$root.$emit('bv::toggle::collapse', 'accordion-' + accordionId);
                    var classHeader = document.getElementById('header-' + accordionId);
                    classHeader.classList.add('not-collapsed');
                }
            },
            //acordions default validations
            validateAccordion(accordionId) {
                switch (accordionId) {
                    case 1: { return true; }
                    case 2: {
                        if (this.manufacturer.name.length === 0) {
                            toast.warning("Please enter Manufacturer name atleast in 1st accordion to continue..");
                            return false;
                        }
                        return true;
                    }
                    case 3: {
                        if (this.manufacturer.name.length === 0) {
                            toast.warning("Please enter Manufacturer name atleast in 1st accordion to continue..");
                            return false;
                        }
                        else if (this.products.filter(product => product.isActive === true).length == 0) {
                            toast.warning("Please choose products in 2nd accordion to continue..");
                            return false;
                        }
                        return true;
                    }
                    case 4: {
                        if (this.manufacturer.name.length === 0) {
                            toast.warning("Please enter Manufacturer name atleast in 1st accordion to continue..");
                            return false;
                        }
                        else if (this.products.filter(product => product.isActive === true && product.product.origin === 2).length === 0) {
                            toast.warning("Imported Manufacturer are only allowed to add Supplier...");
                            return false;
                        }
                        return true;
                    }
                    case 5: {
                        if (this.manufacturer.name.length === 0) {
                            toast.warning("Please enter Manufacturer name atleast in 1st accordion to continue..");
                            return false;
                        }
                        return true;
                    }
                    case 6: {
                        if (this.manufacturer.name.length === 0) {
                            toast.warning("Please enter Manufacturer name atleast in 1st accordion to continue..");
                            return false;
                        }
                        return true;
                    }
                    case 7: {
                        if (!(this.accordion1 && this.accordion2 && this.accordion3 && this.accordion5)) {
                            toast.warning("Please complete all sections to continue..");
                            return false;
                        }
                        else if (!(this.accordion4 || this.products.filter(product => product.isActive === true && product.product.origin === 2).length === 0))
                            return false;
                        return true;
                    }
                    default:
                        return false;
                }
            },
            //brand products
            newBrandProuductsDto() {
                this.objBprod = this.initializeBproductsObj(); this.showNewBProducts = true;
                this.objBprod.product = "";
            },
            bindBrandProductsDto() {
                this.$v.$touch();
                this.brandProductErrors = this.$v.objBprod.$anyError;
                if (!this.brandProductErrors) {
                    if (this.brandProducts.length > 0) {
                        var obj = this.brandProducts.filter(element => element.name === this.objBprod.name);
                        if (obj.length > 0)
                            this.brandProducts.splice(this.brandProducts.indexOf(obj[0]), 1);
                    }
                    this.brandProducts.push(this.objBprod);
                    this.objBprod = this.initializeBproductsObj();
                }
            },
            edidBrandProductsDto(name) {
                var obj = this.brandProducts.filter(element => element.name === name);
                this.objBprod = obj[0];
            },
            removeBrandProductsDto(name) {
                var obj = this.brandProducts.filter(element => element.name === name);
                this.brandProducts.splice(this.brandProducts.indexOf(obj[0]), 1);
            },
            //suppliers
            async loadSupplierDto() {
                this.objSupplier = this.initializeSupplierObj();
                var obj = await this.getSupplierAPI(this.searchSupplierName);
                console.log(JSON.stringify(obj));
                if (obj != null) {
                    this.objSupplier.supplier = {
                        id: obj.id, name: obj.name, identifier1: obj.identifier1, identifier2: obj.identifier2,
                        contact: { id: obj.contact.id, name: obj.contact.name, email: obj.contact.email, countryCode: '+233', phone: obj.contact.phone },
                        address: {
                            id: obj.address.id, description: obj.address.description, postalAddress: obj.address.postalAddress,
                            country: { id: obj.address.country.id, name: obj.address.country.name, regions: obj.address.country.regions },
                            region: {},
                            longitude: obj.address.longitude, latitude: obj.address.latitude
                        }
                    };
                    this.$refs.typeahead.inputValue = this.searchSupplierName = "";
                    this.showOldSupplier = true;
                    this.showNewSupplier = false;
                }
            },
            newSupplierDto() {
                this.objSupplier = this.initializeSupplierObj();
                this.showNewSupplier = true; this.showOldSupplier = false;
                this.objSupplier.supplier.address.country = "";
            },
            bindExistSuppliersDto() {
                if (this.suppliers.length > 0) {
                    var obj = this.suppliers.filter(element => element.supplier.name === this.objSupplier.supplier.name);
                    if (obj.length > 0)
                        toast.error("The Supplier '" + this.objSupplier.supplier.name + "' was already added..");
                    else if (this.objSupplier.supplier.name.length > 0) {
                        this.suppliers.push(this.objSupplier);
                        this.objSupplier = this.initializeSupplierObj();
                    }
                }
                else {
                    this.suppliers.push(this.objSupplier);
                    this.objSupplier = this.initializeSupplierObj();
                }
                this.showOldSupplier = false;
            },
            bindSuppliersDto() {
                this.$v.$touch();
                this.supplierErrors = this.$v.objSupplier.$anyError;
                if (!this.supplierErrors) {
                    if (this.suppliers.length > 0) {
                        var obj = this.suppliers.filter(element => element.supplier.name === this.objSupplier.supplier.name);
                        if (obj.length > 0)
                            this.suppliers.splice(this.suppliers.indexOf(obj[0]), 1);
                    }
                    this.suppliers.push(this.objSupplier);
                    this.objSupplier = this.initializeSupplierObj();
                }
            },
            edidSupplierDto(supplierId, supplierName) {
                if (supplierId !== 0) toast.warning("You can't edit existing Supplier");
                else {
                    var obj = this.suppliers.filter(element => element.supplier.name === supplierName);
                    this.objSupplier = obj[0];
                    this.showNewSupplier = true;
                }
            },
            removeSupplierDto(supplierName) {
                var obj = this.suppliers.filter(element => element.supplier.name === supplierName);
                this.suppliers.splice(this.suppliers.indexOf(obj[0]), 1);
                this.objSupplier = this.initializeSupplierObj();
            },
            //warehouses
            newWarehouseDto() {
                this.objWareHouse = this.initializeWareHouseObj();
                this.objWareHouse.warehouse.address.country = "";
                this.objWareHouse.warehouse.address.region = "";
                this.showNewWarehouse = true;
            },
            bindWarehouseDto() {
                this.$v.$touch();
                this.warehouseErrors = this.$v.objWareHouse.$anyError || this.$v.warehouseConfirmEmailId.$anyError;
                if (!this.warehouseErrors) {
                    if (this.warehouses.length > 0) {
                        var obj = this.warehouses.filter(element => element.warehouse.name === this.objWareHouse.warehouse.name);
                        if (obj.length > 0)
                            this.warehouses.splice(this.warehouses.indexOf(obj[0]), 1);
                    }
                    this.warehouses.push(this.objWareHouse);
                    this.objWareHouse = this.initializeWareHouseObj();
                    this.warehouseConfirmEmailId = '';
                }
            },
            editWarehouseDto(warehouseName) {
                var obj = this.warehouses.filter(element => element.warehouse.name === warehouseName);
                this.objWareHouse = obj[0];
                this.warehouseConfirmEmailId = obj[0].warehouse.contact.email;
                this.showNewWarehouse = true;
            },
            removeWarehouseDto(warehouseName) {
                var obj = this.warehouses.filter(element => element.warehouse.name === warehouseName);
                this.warehouses.splice(this.warehouses.indexOf(obj[0]), 1);
                this.objWareHouse = this.initializeWareHouseObj();
            },
            //uploadFiles
            selectFilesToUpload() {
                this.$refs.file.files.forEach(selectedFile => {
                    var extention = selectedFile.name.split('.').pop();
                    if (Math.round(selectedFile.size / 1024) > 2040) {
                        toast.warning("Upload Failed!! File size is too large - " + selectedFile.name); return;
                    }
                    else if (selectedFile.type !== "application/pdf" && extention !== "docx" ) {
                        toast.warning("Only .pdf and .docx are allowed to upload!! - " + selectedFile.name); return;
                    }
                    else {
                        var date = new Date();
                        var timestamp = Date.UTC(date.getFullYear(), date.getMonth(), date.getDate(), date.getHours(), date.getMinutes(), date.getSeconds(), date.getMilliseconds());
                        this.objFileUpload = this.initializeFileUploadObj();
                        this.objFileUpload.file = selectedFile;
                        this.objFileUpload.fileSize = selectedFile.size;
                        this.objFileUpload.fileName = timestamp + "_" + selectedFile.name;
                        this.objFileUpload.fileURL = this.manufacturerName + '/' + this.objFileUpload.fileName + '';
                        this.objFileUpload.contentType = selectedFile.type;
                        this.objFileUpload.createdDateTime = date.toISOString();
                        this.uploadedFiles.push(this.objFileUpload);
                        this.objFileUpload = this.initializeFileUploadObj();
                    }
                });
            },
            removeSelectedFiles(selectedFile) {
                this.uploadedFiles.splice(selectedFile, 1);
            },
            async uploadFilesToServer() {
                for (let selectedFile of this.uploadedFiles) {
                    const blobServiceClient = new BlobServiceClient(
                        process.env.VUE_APP_BlopStorageBaseUrl + this.sasToken
                    );
                    const containerBaseClient = blobServiceClient.getContainerClient("mfdocs");
                    const blockBlobClient = containerBaseClient.getBlockBlobClient(selectedFile.fileURL);
                    this.runLoader(3000);
                    var result = await blockBlobClient.upload(selectedFile.file, selectedFile.fileName.length);
                    return result;
                }
            },
            //save to database
            async saveAccordion1() {
                this.$v.manufacturerName.$touch();
                this.manufacturerNameErrors = this.$v.manufacturerName.$anyError;
                if(!this.manufacturerNameErrors){
                    this.$v.manufacturer.$touch();
                    this.manufacturerBasicErrors = this.$v.manufacturer.$anyError;
                    if(this.manufacturerBasicErrors){
                        if (confirm("You have not filled all the details. The missed data fields can be filled later. To continue, click on'OK' or to fill in missed data fields now click on 'Cancel'")) {
                            this.lastEditedScreen = 'accordion-1';
                            await this.saveManufacturerDto();
                            await this.$router.push( {name: 'RegisterWithSavedData', params:{Guid : this.$store.getters.manufacturerDto.mfGuid}});
                            await toast.success("Yours Details saved!..");
                            this.toggleAccordionAuto(1);
                        }
                    }
                    else{
                    this.lastEditedScreen = 'accordion-1';
                    await this.saveManufacturerDto();
                    await toast.success("Yours Details saved!..");
                    await this.$router.push( {name: 'RegisterWithSavedData', params:{Guid : this.$store.getters.manufacturerDto.mfGuid}});
                    this.toggleAccordionAuto(1);
                    }
                }
            },
            async saveAccordion2() {
                this.$v.$touch();
                this.productChoosedErrors=this.$v.products.$anyError;
                if(!this.productChoosedErrors){
                this.lastEditedScreen = 'accordion-2';
                await this.saveManufacturerDto();
                toast.success("Products choosed!..");
                this.toggleAccordionAuto(2);
                }
            },
            async saveAccordion3() {
                this.$v.$touch();
                this.brandProductListErrors = this.$v.brandProducts.$anyError;
                if (!this.brandProductListErrors) {
                    this.lastEditedScreen = 'accordion-3';
                    await this.saveManufacturerDto();
                    toast.success("Brand Products Added!..");
                    if (this.products.filter(product => product.isActive === true && product.product.origin === 2).length > 0)
                        await this.toggleAccordionAuto(3);
                    else
                        await this.toggleAccordionAuto(4);
                }
            },
            async saveAccordion4() {
                this.$v.$touch();
                this.supplierListErrors = this.$v.suppliers.$anyError;
                if (!this.supplierListErrors) {
                    this.lastEditedScreen = 'accordion-4';
                    await this.saveManufacturerDto();
                    toast.success("Supplier Details Saved!..");
                    this.toggleAccordionAuto(4);
                }
            },
            async saveAccordion5() {
                this.$v.$touch();
                this.warehouseListErros = this.$v.warehouses.$anyError;
                if (!this.warehouseListErros) {
                    this.lastEditedScreen = 'accordion-5';
                    await this.saveManufacturerDto();
                    toast.success("Warehouse Details Saved!..");
                    this.toggleAccordionAuto(5);
                }
            },
            async saveAccordion6() {
                this.$v.$touch();
                this.uploadFilesErrors = this.$v.uploadedFiles.$anyError || this.$v.fileUploadAcknowledge.$anyError;
                if (!this.uploadFilesErrors) {
                    this.lastEditedScreen = 'accordion-6';
                    await this.$store.dispatch("getSasTokenAsync");
                    var result = await this.uploadFilesToServer();
                    if (result) {
                        toast.success("Files Uploaded!..");
                        await this.saveManufacturerDto();
                        if (!this.disableCommentSection)
                            await this.toggleAccordionAuto(6);
                    }
                    else
                        toast.error("File Upload failed!..Please try again..");
                }
            },
            async saveAccordion7() {
                this.$v.$touch();
                this.commentErrors = this.$v.newComment.$anyError;
                if (!this.commentErrors) {
                    this.lastEditedScreen = 'accordion-7';
                    toast.success("Comments Added!..");
                    await this.saveManufacturerDto();
                    this.toggleAccordion(7);
                }
            },
            async saveManufacturerDto() {
                var objManufacturerDto = this.manufacturer;
                //Choosed Products
                objManufacturerDto.products = this.products.filter(product => product.isActive === true),
                    //choosed Brand Products
                    objManufacturerDto.brandProducts = this.brandProducts,
                    //choosed Suppliers
                    objManufacturerDto.suppliers = this.suppliers,
                    //choosed WareHouses
                    objManufacturerDto.warehouses = this.warehouses,
                    //initialy fileuploadId will be 0, after first save it will get the value from database
                    objManufacturerDto.fileUploadId = this.fileUploadId;
                //choosed UploadFiles
                objManufacturerDto.uploadedFiles = this.uploadedFiles,
                    //choosed approvals
                    objManufacturerDto.approvals = null;
                objManufacturerDto.Comments = this.newComment;
                //initialy mfGuid will be 0, after first save it will get the value from database
                objManufacturerDto.mfGuid = this.mfGuid;
                //initialy id will be 0, after first save it will get the value from database
                objManufacturerDto.id = this.id;
                //user saved accordian name
                objManufacturerDto.lastEditedScreen = this.lastEditedScreen;
                this.runLoader(5000);
                await this.$store.dispatch("addManufacturerDtoSaveAsync", objManufacturerDto);
                await this.getManufacturerDto();
            },
            //preview Details
            showPreview() {
                if (!(this.accordion1 && this.accordion2 && this.accordion3 && this.accordion5))
                    return false;
                else if (!(this.accordion4 || this.products.filter(product => product.isActive === true && product.product.origin === 2).length === 0))
                    return false;
                return true;
            },
            previewManufacturerDto() {
                this.$router.push( {name: 'ManufacturerPreview'});
            },
            runLoader(sec) {
                document.getElementById("loader").style.display = "block";
                setInterval(() => {
                    document.getElementById("loader").style.display = "none";
                }, sec);
            },
        }
    }
</script>