import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios"
import VueBootstrapTypeahead from 'vue-bootstrap-typeahead'
Vue.use(Vuex)
Vue.component('vue-bootstrap-typeahead', VueBootstrapTypeahead)
const _apiBaseURLforVendor = process.env.VUE_APP_VmApisBaseUrl;
const _apiBaseURLforProduct =process.env.VUE_APP_ProdMgmtApisBaseUrl;
export default new Vuex.Store({
    state: {
        manufacturerDto: {},countries: [],products: [],
        manufacturerGuid: '',suppliersName:[],sasToken:''
    },
    mutations: {
        addUpdateManufacturerDto(state, objManufacturer) {
            if(objManufacturer!=null) Vue.set(state, 'manufacturerDto', objManufacturer);
        },
        addProducts(state, arrProducts) {
            if(arrProducts!=null){
            state.products = [];
            arrProducts.forEach(element => {state.products.push(element);});
            }
        },
        addsuppliers(state, arrSuppliers) {
            if(arrSuppliers!=null){
            state.suppliersName = [];
            arrSuppliers.forEach(element => {state.suppliersName.push(element);});
            }
        },
        addUpdateCountry(state, arrCountries) {
            if(arrCountries!=null){
            state.countries = [];
            arrCountries.forEach(element => {state.countries.push(element);});
            }
        },
        updateManufacturerGuid(state, manufacturerGuid) {
            if(manufacturerGuid!=null){
            state.manufacturerGuid = '';
            Vue.set(state, 'manufacturerGuid', manufacturerGuid);
            }
        },
        AddsasToken(state, arrToken){
            if(arrToken!=null){
            state.sasToken = '';
            this.state.sasToken = arrToken;
            }
        }
    },
    actions: {
        //GET Calls
        getManufacturerDtoByIdAsync: async({ commit }, manufacturerId) => {
            await axios.get(_apiBaseURLforVendor + "/Manufacturer/"+manufacturerId).then(resp => {commit('addUpdateManufacturerDto', resp.data);});
        },
        getManufacturerDtoSavedDataByGuidAsync: async({ commit }, manufacturerGuid) => {
            await axios.get(_apiBaseURLforVendor + "/Manufacturer/GetManufacturerSavedDataForRegistration/" + manufacturerGuid).then(resp => {commit('addUpdateManufacturerDto', resp.data);});
        },
        getProductsAsync: async({ commit }) => {
            await axios.get(_apiBaseURLforProduct+"/Product/").then(resp => {commit('addProducts', resp.data);});
        },
        getSuppliersAsync: async({ commit }) => {
            await axios.get(_apiBaseURLforVendor+"/Supplier/GetAllSuppliersName").then(resp => { commit('addsuppliers', resp.data) });
        },
        getCountriesAsync: async({ commit }) => {
            await axios.get(_apiBaseURLforVendor+'/MasterData/GetAllCountryDetails').then(resp => {commit('addUpdateCountry', resp.data)});
        },
        getSasTokenAsync: async({commit}) =>{
            await axios.get(_apiBaseURLforVendor + "/Storage/GetSASToken/").then(resp=> {return commit('AddsasToken', resp.data)});
        }, 
        //POST Calls
        addManufacturerDtoAsync: async({ commit }, arrManufacturerDto) => {
            await axios.post(_apiBaseURLforVendor+"/Manufacturer/", arrManufacturerDto).then(resp => {commit('addUpdateManufacturerDto', resp.data) });
        },
        addManufacturerDtoSaveAsync: async({ commit }, arrManufacturerDto) => {
            await axios.post(_apiBaseURLforVendor + "/Manufacturer/Save", arrManufacturerDto).then(resp => { commit('updateManufacturerGuid', resp.data);});
        }
    },
    getters: {
        manufacturerDto: state => state.manufacturerDto,
        countries: state => state.countries,
        products: state => state.products,
        manufacturerGuid: state => state.manufacturerGuid,
        suppliersName:state=>state.suppliersName,
        sasToken: state => state.sasToken,
    }
})