import axios from 'axios'

const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,24}))$/;
export function checkNullOrEmpty(value){return !(value===undefined || value===null ||value.length===0);}
export function checkEmptyNumber(value=''){ return !isNaN(value);}
export async function checkEmailDuplicate(value){
    try {
        if(value.length===0)return true;
        if(!(pattern.test(String(value).toLowerCase()))) return true;
        const response = await axios.get(process.env.VUE_APP_UmApisBaseUrl + '/User/CheckDuplicateEmail?emailId=' + value);
        if(response.data===true){
            const response1 = await axios.get(process.env.VUE_APP_VmApisBaseUrl + '/Manufacturer/CheckDuplicateEmail/' + value);
            return response1.data;
        }
        return response.data;
    } catch (error) {
        console.log(error);
    }
}
